import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/home/Home'
import Skill from '../components/skill/Skill'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/skill',
    name: 'Skill',
    component: Skill
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
