<template>
  <div class="barrs">

            <div class="row" v-html="barrsBall()">

            </div>
  </div>
</template>

<script>
export default {
    name: 'Barrs',
    props: ['lengthOn'],
    data: function() {
    return {
            imageOn: require('@/assets/image/circleOn.png'), 
            imageOff: require('@/assets/image/circleOff.png'), 
        };
    },
    methods: {
        colorOn: function(und){
            let retunrHtml = '';
            while (und > 0) {
                retunrHtml = retunrHtml + '<img src="'+this.imageOn+'">';
                und -= 1;
            }
            return retunrHtml;
        },
        colorOff: function(und){
            let retunrHtml = '';
            while (und > 0) {
                retunrHtml = retunrHtml + '<img src="'+this.imageOff+'">';
                und -= 1;
            }
            return retunrHtml;
        },
        barrsBall: function() {
            let bon = this.lengthOn;
            let boff = 5 - bon;
            let stringOn = this.colorOn(bon);
            let stringOff = this.colorOff(boff);
            return  stringOn + stringOff ;
        }
    } 

}
</script>

